.sidebar {
    background-color: $color-1;
    font-family: $font-1;
    min-height: $minHeight;
    width: 264px;
    border-radius: $borderRadius;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 250px 1fr 108px;
    grid-template-areas: "1" "2" "3";

    .id {
        grid-area: 1;
        text-align: center;
        user-select: none;
        position: relative;

        .idContent {
            position: absolute;
            bottom: 0%;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;

            img {
                max-width: 150px;
                border-radius: 50%;
                box-shadow: 0 0 0 4px $color-4;
            }

            h2 {
                font-size: 1.3rem;
                text-transform: uppercase;
                letter-spacing: 0.2rem;
                margin-top: 1.5rem;
                color: $color-4;
            }
        }
    }

    .navigation {
        grid-area: 2;
        padding: 2rem 1rem;
        align-self: center;

        ul {
            height: 100%;
            display: grid;

            li {
                padding: 1rem 0 1rem 0.4rem;
                font-size: 1.3rem;
                position: relative;

                &:hover a {
                    color: $color-4;
                }

                &:hover i {
                    transform: translate(3px, 3px);
                }

                a {
                    color: $color-3;
                }

                .navActive {
                    color: $color-4;
                }

                i {
                    position: absolute;
                    font-size: 0.95rem;
                    transform: translateY(3px);
                    transition: .2s ease;
                    color: $color-4;
                }

                span {
                    margin-left: 2.7rem;
                }
            }
        }
    }

    .socialMedia {
        grid-area: 3;
        margin: auto;

        ul {
            display: flex;
            width: 264px;
            padding: 0 14px;
            justify-content: space-between;

            li {
                font-size: 1.5rem;
                background-color: $color-3;
                color: $color-1;
                border-radius: 50%;
                padding: 0.5rem 0.8rem;
                transition: 0.3s;
                cursor: pointer;

                &:hover {
                    background-color: $color-4;
                }
            }
        }
    }

    .signature {
        margin-top: 2rem;
        text-align: center;
        letter-spacing: 0.1rem;
        font-size: 0.7rem;

        p, a {
            color: $color-3; 
            padding-bottom: 2rem;
        }

        a:hover {
            color: $color-4;
        }
    }
}

@media screen and (max-width: 850px) {
    .sidebar {
        display: flex;
        min-height: 50px;
        margin-bottom: 0.2rem;
        width: 100%;

        .id, .socialMedia, .signature {
            display: none;
        }

        .navigation {
            width: 100%;
            padding: 0.1rem 1rem;

            ul {
                display: flex;
                justify-content: space-between;

                span {
                    margin-left: 1.5rem !important;
                }
            }
        }
    }
}

@media screen and (max-width: 700px) {
    .sidebar {
        ul {
            justify-content: space-around !important;

            li {
                padding: 1rem 0 !important;

                i {
                    font-size: 1.4rem !important;
                    transform: translateY(-10px) !important;
                }

                span {
                    display: none;
                }
            }
        }
    }
}