.portfolio {
	.portfolioContent {
		width: 100%;
		display: grid;
		min-height: $minHeight;
		grid-template-columns: 1fr;
		grid-template-rows: 70px 1fr;
		grid-template-areas: "1" "2";
	}

	.radioDisplay {
		grid-area: 1;
		border-radius: $borderRadius;
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		padding: 1rem 2rem;
		margin: 0 auto;
		background: $color-1;
		user-select: none;
		box-shadow: $shadow;
		z-index: -1;

		li {
			color: $color-3;
			font-size: 1.1rem;
			text-transform: uppercase;
			padding: 0 1rem;
			flex: 1;
			z-index: 2;
			@include verticalCenter;

			input[type="radio"] {
				visibility: hidden;
				position: relative;

				&:before {
					content: "";
					visibility: visible;
					position: absolute;
					top: -4px;
					left: -12px;
					height: 0.9rem;
					width: 0.9rem;
					border-radius: 50%;
					border: 2px solid $color-4;
					cursor: pointer;
					transition: 0.3s;
				}

				&:after {
					content: "";
					visibility: visible;
					position: absolute;
					top: -4px;
					left: -12px;
					height: 0.9rem;
					width: 0.9rem;
					border-radius: 50%;
					cursor: pointer;
					transition: 0.3s;
					background: $color-4;
					transform: scale(0);
					transform-origin: center;
				}
			}

			input:checked:after {
				transform: scale(1.17) translate(-25%, -25%);
				top: 2px;
				left: -6px;
			}

			input:checked + label {
				color: $color-4;
			}

			label {
				cursor: pointer;
			}
		}
	}
	.projects {
		grid-area: 2;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;

		.project {
			background: $color-3;
			border-radius: $borderRadius;
			margin: 0.5rem 0 0 0;
			text-align: center;
			width: 32%;
			max-height: 50%;
			padding: 0.3rem;
			box-shadow: $shadow;
			display: grid;
			grid-template-rows: 15% 15% 55% 15%;
			grid-template-areas:
				"1"
				"2"
				"3"
				"4";
		}

		.icons {
			grid-area: 1;
			display: flex;
			justify-content: center;
			@include verticalCenter;

			i {
				font-size: 1.4rem;
				padding: 0 0.35rem;
				color: $color-4;
				text-shadow: 0 0px 1px $color-1;
			}
		}

		h3 {
			grid-area: 2;
			font-family: $font-2;
			font-size: 1.2rem;
			@include verticalCenter;
			justify-content: center;
		}

		img {
			grid-area: 3;
			object-fit: cover;
			margin: 0 auto;
			width: 100%;
			height: 80%;
			box-shadow: 0 2px 6px #333;
			border-radius: 4px;
			cursor: pointer;
			@include verticalCenter;
		}

		.infos {
			grid-area: 4;
			color: $color-4;
			cursor: pointer;
			font-size: 1.5rem;
			@include verticalCenter;
			transition: 0.2s;

			&:hover {
				transform: scale(1.1);
			}
		}

		.showInfos {
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			background: rgba(0, 0, 0, 0.507);
			animation: popup 0.3s ease forwards;
			transform-origin: center;

			.infosContent {
				position: relative;
				top: 25%;
				width: 600px;
				margin: 0 auto;
				color: $color-4;
				background: $color-1;
				border-radius: $borderRadius;
				padding: 2rem;
				box-shadow: 0 10px 10px #333;
				animation: popup 0.6s ease forwards;
				transform: scale(0);
				transform-origin: center;

				@keyframes popup {
					to {
						transform: scale(1);
					}
				}

				.head {
					display: flex;
					justify-content: space-between;
					align-items: center;
					margin: 1rem 0 2rem 0;
	
					.headTitle {
						display: flex;
						align-items: center;
	
						.returnButton {
							cursor: pointer;
							color: $color-4;
	
							&:hover {
								color: $color-3;
							}
						}
	
						h2 {
							margin: 0 0 0 2rem;
						}
					}
	
					.sourceButton {
						color: $color-1;
						background-color: $color-3;
						padding: 0.8rem;
						border-radius: 20px;
						margin: 2rem;
	
						&:hover {
							background-color: $color-4;
						}
					}
				}
	
				.text {
					text-align: justify;
					margin-bottom: 1rem;
					color: $color-3;
				}
			}
		}
	}
}

@media (max-width: 850px) {
	.radioDisplay {
		display: flex;
		flex-wrap: wrap;
	}
}

@media (max-width: 650px) {
	.portfolioContent {
		.project {
			width: 49% !important;

			i {
				font-size: 1rem;
			}
		}
		.infosContent {
			width: 80% !important;
		}
	}
}

@media (max-width: 580px) {
	.radioDisplay {
		display: none !important;
	}

	.portfolioContent {
		grid-template-rows: 1fr !important;

		.project {
			grid-template-rows: 30px 40px 80px 40px !important;
			width: 98% !important;

			img {
				height: 100% !important;
			}
		}
	}
}

@media (max-width: 500px) {
	.head {
		display: flex;
		flex-wrap: wrap;

		.headTitle {
			font-size: 14px;
		}
		.sourceButton {
			margin: 0 0.5rem !important; 
			padding: 0.5rem !important;
			font-size: 12px;
		}
	}
	
}

@media screen and (max-width: 420px) {
	
	.portfolio .projects .showInfos {
		.infosContent {
			padding: 0.5rem;

			.head .sourceCode .sourceButton {
				margin: 1rem 0.5rem !important; 
			}
		}

		

	}
		
	
	
	
}